import React, { useEffect, useState } from 'react';
import Input from './components/Input';

const App = () => {
  const [value, setValue] = useState();
  const handleChange = (e) => setValue(e.target.value);
  
  const prettifyResult = (num)=> {
    const a = num.toString().split('');
    a.splice(2,0,'.');
    const b = a;
    return b.join('');
  }

  const calculateResult = (num) => {
    if (!num || num <= 0) {
      return;
    }
    
    const decimalNum = num >= 100 ? prettifyResult(num) : num;
    const dividedResult = 235.215/decimalNum;
    return Number(dividedResult.toString().slice(0,5));
  }

  return (
    <div className="app">
      <h1>Mpg to l/100km</h1>
      <Input value={value} onChange={handleChange}/>
      <h2>Result:</h2>
      {calculateResult(value)}
    </div>
  );
}

export default App;
